
"use client"

import { useAuth } from "@/components/Auth/AuthProvider";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator"
import { ArrowLeft } from "lucide-react"
import { useEffect, useState } from "react";
import { User } from "firebase/auth";
import Link from "next/link";

interface UserAttr {
  role?: string;
  fullName?: string;
};
type AppUser = User & UserAttr;

export default function ProfilePage() {
  const [userData, setUserData] = useState<AppUser | null>(null);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch("/api/user");
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        setUserData(data);
      } catch (error) {
        console.error("Error fetching user data:", error);
        // Handle the error appropriately, e.g., display an error message to the user
      }
    };
    fetchUserData();
  }, []);
  const router = useRouter();
  console.log(userData);
  useEffect(() => {
    if (!userData) {
      router.push('/login');
    }
  }, [userData, router]);

  return (
    <div className="flex flex-col justify-center items-center py-10 bg-secondary gap-4 w-full min-h-screen">
      <div className="w-full max-w-2xl px-4">
        <Link href="/" className="flex items-center text-blue-500 hover:underline mb-4">
          <ArrowLeft className="mr-2 h-4 w-4" />
          Back
        </Link>
        <Card className="w-full">
          <CardHeader className="p-6 pb-0">
            <CardTitle className="text-3xl font-bold">
            <h1 className="text-3xl font-bold">Profile Details</h1>
            </CardTitle>
          </CardHeader>
          <Separator className="my-4" />
          <CardContent className="p-6 pt-2 space-y-4">
            <div className="grid gap-2">
              <div className="flex flex-col gap-y-1">
                <span className="font-semibold">Full Name:</span>
                <span className="text-gray-600">{userData?.fullName}</span>
              </div>
              <div className="flex flex-col gap-y-1">
                <span className="font-semibold">Email:</span>
                <span className="text-gray-600">{userData?.email}</span>
              </div>
              <div className="flex flex-col gap-y-1">
                <span className="font-semibold">Role:</span>
                <span className="text-gray-600">{userData?.role}</span>
              </div>
            </div>
          </CardContent>
        </Card>        
      </div>
    </div>
  )
}